import { ReactNode } from 'react';

export enum ChangeType {
    'ADD_NODE',
    'ADD_LINE',
    'LAYOUT',
    'DELETE',
    'EDIT_LINE',
    'EDIT_NODE',
}

export enum NodeTypes {
    NORMAL_NODE = '',
    TEMPLATE_NODE = '',
    ANCHOR = '',
    LINE_POINT = ''
}

export enum KeyCode {
    BACKSPACE = 8,
    DELETE = 46,
}
/**  */
export enum SelectedType {
    NODE,
    LINE,
    NONE,
}

/** 
 * ADD: 
 * EDIT_START: 
 * EDIT_END: 
 */
export enum LineEditType {
    ADD = 'add',
    EDIT_START = 'start',
    EDIT_END = 'end',
}

export interface IPosition {
    x: number;
    y: number;
}

export interface ITopologyNode {
    id: string;
    position?: IPosition;
    canDrag?: boolean; // 
    dragChild?: boolean; // 
    filterOverlap?: boolean; //  overlap  true 
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    [prop: string]: any;
}

export interface ITopologyLine {
    start: string;
    end: string;
    color?: string;
}

export interface ITopologyContext {
    linking: boolean;
    impactNode: string | null;
    readOnly: boolean;
    hoverCurrentNode: ITopologyNode;
    activeLine: {
        start: IPosition;
        end: IPosition;
        type: LineEditType;
        /**  */
        origin: ITopologyLine | null;
    } | null;
    selectedData: {
        nodes: ITopologyNode[];
        lines: ITopologyLine[];
    };
}

export interface ITopologyData {
    nodes: ITopologyNode[];
    lines: ITopologyLine[];
}

export interface IWrapperOptions {
    anchorDecorator: (options: { anchorId?: string }) => (item: ReactNode) => ReactNode;
}

export type ValuesOf<T> = {
    [P in keyof T]?: T[P];
};
